"use client";

import type React from "react";

import { useState, useCallback, useRef } from "react";
import useSWR, { mutate } from "swr";
import { toast } from "sonner";
import {
  Folder,
  File,
  Upload,
  FolderPlus,
  Download,
  Trash2,
  ChevronRight,
  Home,
  Loader2,
  Search,
  Grid,
  List,
  MoreVertical,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { cn } from "@/lib/utils";
import Image from "next/image";
import { useDebounce } from "@/hooks/use-debounce";

interface FileItem {
  id: string;
  name: string;
  originalName: string;
  size: number;
  compressedSize: number;
  type: string;
  folderId: string | null;
  createdAt: string;
}

interface FolderItem {
  id: string;
  name: string;
  parentId: string | null;
  createdAt: string;
}

const fetcher = (url: string) => fetch(url).then((res) => res.json());

function formatBytes(bytes: number): string {
  if (bytes === 0) return "0 B";
  const k = 1024;
  const sizes = ["B", "KB", "MB", "GB"];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return (
    Number.parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + " " + sizes[i]
  );
}

function getCompressionRatio(original: number, compressed: number): string {
  if (original === 0) return "0%";
  const ratio = ((1 - compressed / original) * 100).toFixed(1);
  return `${ratio}% saved`;
}

function getFileIcon(type: string) {
  if (type.startsWith("image/")) return "üñºÔ∏è";
  if (type.startsWith("video/")) return "üé¨";
  if (type.startsWith("audio/")) return "üéµ";
  if (type.includes("pdf")) return "üìÑ";
  if (type.includes("zip") || type.includes("rar") || type.includes("7z"))
    return "üì¶";
  if (type.includes("text") || type.includes("document")) return "üìù";
  return "üìé";
}

export function FileExplorer() {
  const [currentFolderId, setCurrentFolderId] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [newFolderName, setNewFolderName] = useState("");
  const [folderDialogOpen, setFolderDialogOpen] = useState(false);
  const [dragActive, setDragActive] = useState(false);
  const [viewMode, setViewMode] = useState<"grid" | "list">("grid");
  const [searchQuery, setSearchQuery] = useState("");
  const debouncedSearch = useDebounce(searchQuery, 500);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const { data: files = [], isLoading: filesLoading } = useSWR<FileItem[]>(
    `/api/files?folderId=${currentFolderId || ""}&search=${debouncedSearch}`,
    fetcher
  );

  const { data: folders = [], isLoading: foldersLoading } = useSWR<
    FolderItem[]
  >(
    debouncedSearch ? null : `/api/folders?parentId=${currentFolderId || ""}`,
    fetcher
  );

  const { data: breadcrumbs = [] } = useSWR<FolderItem[]>(
    currentFolderId && !debouncedSearch
      ? `/api/folders?breadcrumbs=${currentFolderId}`
      : null,
    fetcher
  );

  const refreshData = useCallback(() => {
    mutate(
      `/api/files?folderId=${currentFolderId || ""}&search=${debouncedSearch}`
    );
    if (!debouncedSearch) {
      mutate(`/api/folders?parentId=${currentFolderId || ""}`);
    }
  }, [currentFolderId, debouncedSearch]);

  const handleFileUpload = async (fileList: FileList | null) => {
    if (!fileList || fileList.length === 0) return;

    setIsUploading(true);

    for (const file of Array.from(fileList)) {
      const formData = new FormData();
      formData.append("file", file);
      if (currentFolderId) {
        formData.append("folderId", currentFolderId);
      }

      try {
        const response = await fetch("/api/files", {
          method: "POST",
          body: formData,
        });

        if (!response.ok) throw new Error("Upload failed");

        const uploadedFile = await response.json();
        toast.success(`Uploaded ${file.name}`, {
          description: `Compressed: ${getCompressionRatio(
            uploadedFile.size,
            uploadedFile.compressedSize
          )}`,
        });
      } catch {
        toast.error(`Failed to upload ${file.name}`);
      }
    }

    setIsUploading(false);
    refreshData();
  };

  const handleCreateFolder = async () => {
    if (!newFolderName.trim()) return;

    try {
      const response = await fetch("/api/folders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: newFolderName.trim(),
          parentId: currentFolderId,
        }),
      });

      if (!response.ok) throw new Error("Failed to create folder");

      toast.success(`Created folder: ${newFolderName}`);
      setNewFolderName("");
      setFolderDialogOpen(false);
      refreshData();
    } catch {
      toast.error("Failed to create folder");
    }
  };

  const handleDownload = async (file: FileItem) => {
    try {
      // Direct download link
      const a = document.createElement("a");
      a.href = `/api/files/${file.id}`;
      a.download = file.originalName;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);

      toast.success(`Downloading ${file.originalName}`);
    } catch {
      toast.error("Failed to download file");
    }
  };

  const handleDeleteFile = async (file: FileItem) => {
    try {
      const response = await fetch(`/api/files/${file.id}`, {
        method: "DELETE",
      });
      if (!response.ok) throw new Error("Delete failed");

      toast.success(`Deleted ${file.originalName}`);
      refreshData();
    } catch {
      toast.error("Failed to delete file");
    }
  };

  const handleDeleteFolder = async (folder: FolderItem) => {
    try {
      const response = await fetch(`/api/folders/${folder.id}`, {
        method: "DELETE",
      });
      if (!response.ok) throw new Error("Delete failed");

      toast.success(`Deleted folder: ${folder.name}`);
      refreshData();
    } catch {
      toast.error("Failed to delete folder");
    }
  };

  const handleDrag = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
    } else if (e.type === "dragleave") {
      setDragActive(false);
    }
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    handleFileUpload(e.dataTransfer.files);
  };

  const isLoading = filesLoading || foldersLoading;

  return (
    <div className="min-h-screen bg-background text-foreground font-sans selection:bg-primary/20">
      {/* Header */}
      <header className="border-b border-border/40 bg-background/80 backdrop-blur-md sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between gap-4">
          <div className="flex items-center gap-3">
            <div className="relative w-8 h-8">
              <Image
                src="/icon.png"
                alt="LanDrop Logo"
                fill
                className="object-contain"
              />
            </div>
            <h1 className="text-xl font-bold tracking-tight">
              Lan<span className="text-primary">Drop</span>
            </h1>
          </div>

          <div className="flex-1 max-w-md hidden md:block">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
              <Input
                placeholder="Search files..."
                className="pl-9 bg-secondary/50 border-transparent focus:bg-background focus:border-primary/50 transition-all"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
          </div>

          <div className="flex items-center gap-2">
            <Dialog open={folderDialogOpen} onOpenChange={setFolderDialogOpen}>
              <DialogTrigger asChild>
                <Button
                  variant="secondary"
                  className="gap-2 bg-secondary/80 hover:bg-secondary text-foreground"
                >
                  <FolderPlus className="w-4 h-4" />
                  <span className="hidden sm:inline">New Folder</span>
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Create New Folder</DialogTitle>
                </DialogHeader>
                <div className="flex gap-2 mt-4">
                  <Input
                    placeholder="Folder name"
                    value={newFolderName}
                    onChange={(e) => setNewFolderName(e.target.value)}
                    onKeyDown={(e) => e.key === "Enter" && handleCreateFolder()}
                  />
                  <Button onClick={handleCreateFolder}>Create</Button>
                </div>
              </DialogContent>
            </Dialog>

            <label>
              <Button
                className="gap-2 cursor-pointer shadow-lg shadow-primary/20 text-primary-foreground font-medium"
                disabled={isUploading}
              >
                {isUploading ? (
                  <Loader2 className="w-4 h-4 animate-spin" />
                ) : (
                  <Upload className="w-4 h-4" />
                )}
                <span className="hidden sm:inline">Upload</span>
              </Button>
              <input
                ref={fileInputRef}
                type="file"
                multiple
                className="hidden"
                onChange={(e) => handleFileUpload(e.target.files)}
                disabled={isUploading}
              />
            </label>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-8">
        {/* Navigation Bar */}
        <div className="flex flex-col md:flex-row items-start md:items-center justify-between gap-4 mb-8">
          <nav className="flex items-center flex-wrap gap-1 text-sm bg-secondary/30 p-1 rounded-lg">
            <Button
              variant="ghost"
              size="sm"
              className="h-8 px-3 gap-2 hover:bg-background text-muted-foreground hover:text-foreground"
              onClick={() => {
                setCurrentFolderId(null);
                setSearchQuery("");
              }}
            >
              <Home className="w-4 h-4" />
              <span>Home</span>
            </Button>
            {breadcrumbs.map((crumb) => (
              <div key={crumb.id} className="flex items-center">
                <ChevronRight className="w-4 h-4 text-muted-foreground/50" />
                <Button
                  variant="ghost"
                  size="sm"
                  className="h-8 px-3 hover:bg-background text-muted-foreground hover:text-foreground"
                  onClick={() => setCurrentFolderId(crumb.id)}
                >
                  {crumb.name}
                </Button>
              </div>
            ))}
          </nav>

          <div className="flex items-center gap-2 bg-secondary/30 p-1 rounded-lg self-end">
            <Button
              variant={viewMode === "grid" ? "secondary" : "ghost"}
              size="sm"
              className="h-8 w-8 p-0"
              onClick={() => setViewMode("grid")}
            >
              <Grid className="w-4 h-4" />
            </Button>
            <Button
              variant={viewMode === "list" ? "secondary" : "ghost"}
              size="sm"
              className="h-8 w-8 p-0"
              onClick={() => setViewMode("list")}
            >
              <List className="w-4 h-4" />
            </Button>
          </div>
        </div>

        {/* Drop Zone */}
        <div
          className={cn(
            "border-2 border-dashed rounded-2xl p-12 mb-10 transition-all duration-300 ease-in-out cursor-pointer",
            dragActive
              ? "border-primary bg-primary/5 scale-[1.01] shadow-xl shadow-primary/10"
              : "border-border/50 hover:border-primary/30 hover:bg-secondary/20"
          )}
          onDragEnter={handleDrag}
          onDragLeave={handleDrag}
          onDragOver={handleDrag}
          onDrop={handleDrop}
          onClick={() => fileInputRef.current?.click()}
        >
          <div className="flex flex-col items-center text-center gap-4">
            <div
              className={cn(
                "p-4 rounded-full transition-colors",
                dragActive ? "bg-primary/20" : "bg-secondary"
              )}
            >
              <Upload
                className={cn(
                  "w-8 h-8 transition-colors",
                  dragActive ? "text-primary" : "text-muted-foreground"
                )}
              />
            </div>
            <div>
              <p className="text-lg font-medium">Drag and drop files here</p>
              <p className="text-sm text-muted-foreground mt-1">
                or click to browse
              </p>
            </div>
          </div>
        </div>

        {isLoading ? (
          <div className="flex flex-col items-center justify-center py-20 gap-4">
            <Loader2 className="w-10 h-10 animate-spin text-primary" />
            <p className="text-muted-foreground animate-pulse">
              Loading content...
            </p>
          </div>
        ) : (
          <div className="space-y-10">
            {/* Search Results Header */}
            {debouncedSearch && (
              <div className="flex items-center gap-2 text-muted-foreground">
                <Search className="w-4 h-4" />
                <span>Search results for "{debouncedSearch}"</span>
              </div>
            )}

            {/* Folders */}
            {!debouncedSearch && folders.length > 0 && (
              <section>
                <h2 className="text-sm font-semibold text-muted-foreground uppercase tracking-wider mb-4 px-1">
                  Folders
                </h2>
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 gap-4">
                  {folders.map((folder) => (
                    <Card
                      key={folder.id}
                      className="group relative p-4 cursor-pointer bg-card hover:bg-secondary/50 border-border/50 hover:border-primary/30 transition-all duration-200 hover:-translate-y-1 hover:shadow-lg hover:shadow-primary/5"
                      onClick={() => setCurrentFolderId(folder.id)}
                    >
                      <div className="flex flex-col items-center gap-3 py-2">
                        <Folder className="w-12 h-12 text-primary fill-primary/20 transition-transform group-hover:scale-110" />
                        <span className="text-sm font-medium text-center truncate w-full">
                          {folder.name}
                        </span>
                      </div>

                      <div className="absolute top-2 right-2 opacity-0 group-hover:opacity-100 transition-opacity">
                        <DropdownMenu>
                          <DropdownMenuTrigger asChild>
                            <Button
                              variant="ghost"
                              size="icon"
                              className="h-6 w-6"
                              onClick={(e) => e.stopPropagation()}
                            >
                              <MoreVertical className="w-3 h-3" />
                            </Button>
                          </DropdownMenuTrigger>
                          <DropdownMenuContent align="end">
                            <DropdownMenuItem
                              className="text-destructive focus:text-destructive"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleDeleteFolder(folder);
                              }}
                            >
                              <Trash2 className="w-4 h-4 mr-2" />
                              Delete
                            </DropdownMenuItem>
                          </DropdownMenuContent>
                        </DropdownMenu>
                      </div>
                    </Card>
                  ))}
                </div>
              </section>
            )}

            {/* Files */}
            {files.length > 0 && (
              <section>
                <h2 className="text-sm font-semibold text-muted-foreground uppercase tracking-wider mb-4 px-1">
                  Files
                </h2>
                {viewMode === "grid" ? (
                  <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
                    {files.map((file) => (
                      <Card
                        key={file.id}
                        className="group relative p-4 bg-card hover:bg-secondary/50 border-border/50 hover:border-primary/30 transition-all duration-200 hover:shadow-md"
                      >
                        <div className="flex items-start justify-between gap-3">
                          <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-secondary text-xl shrink-0">
                            {getFileIcon(file.type)}
                          </div>
                          <div className="flex-1 min-w-0">
                            <p
                              className="font-medium truncate text-sm"
                              title={file.originalName}
                            >
                              {file.originalName}
                            </p>
                            <p className="text-xs text-muted-foreground mt-1">
                              {formatBytes(file.size)}
                            </p>
                          </div>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button
                                variant="ghost"
                                size="icon"
                                className="h-8 w-8 -mr-2 -mt-2 opacity-0 group-hover:opacity-100 transition-opacity"
                              >
                                <MoreVertical className="w-4 h-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem
                                onClick={() => handleDownload(file)}
                              >
                                <Download className="w-4 h-4 mr-2" />
                                Download
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                className="text-destructive focus:text-destructive"
                                onClick={() => handleDeleteFile(file)}
                              >
                                <Trash2 className="w-4 h-4 mr-2" />
                                Delete
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </div>
                        <div className="mt-4 flex items-center justify-between text-xs text-muted-foreground">
                          <span>
                            {new Date(file.createdAt).toLocaleDateString()}
                          </span>
                          <span className="text-primary font-medium bg-primary/10 px-1.5 py-0.5 rounded">
                            {getCompressionRatio(
                              file.size,
                              file.compressedSize
                            )}
                          </span>
                        </div>
                      </Card>
                    ))}
                  </div>
                ) : (
                  <div className="space-y-2">
                    {files.map((file) => (
                      <Card
                        key={file.id}
                        className="group flex items-center gap-4 p-3 hover:bg-secondary/50 border-border/50 transition-colors"
                      >
                        <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-secondary text-lg shrink-0">
                          {getFileIcon(file.type)}
                        </div>
                        <div className="flex-1 min-w-0 grid grid-cols-1 md:grid-cols-4 gap-4 items-center">
                          <div className="md:col-span-2">
                            <p className="font-medium truncate text-sm">
                              {file.originalName}
                            </p>
                          </div>
                          <div className="text-xs text-muted-foreground">
                            {formatBytes(file.size)}
                          </div>
                          <div className="text-xs text-muted-foreground">
                            {new Date(file.createdAt).toLocaleDateString()}
                          </div>
                        </div>
                        <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8"
                            onClick={() => handleDownload(file)}
                          >
                            <Download className="w-4 h-4" />
                          </Button>
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8 text-destructive hover:text-destructive"
                            onClick={() => handleDeleteFile(file)}
                          >
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </div>
                      </Card>
                    ))}
                  </div>
                )}
              </section>
            )}

            {/* Empty State */}
            {folders.length === 0 && files.length === 0 && (
              <div className="flex flex-col items-center justify-center py-20 text-center">
                <div className="w-20 h-20 rounded-full bg-secondary/50 flex items-center justify-center mb-6">
                  <Folder className="w-10 h-10 text-muted-foreground/50" />
                </div>
                <h3 className="text-xl font-semibold text-foreground mb-2">
                  No files found
                </h3>
                <p className="text-muted-foreground max-w-sm mx-auto">
                  {debouncedSearch
                    ? "No files match your search query."
                    : "This folder is empty. Upload files or create a new folder to get started."}
                </p>
                {!debouncedSearch && (
                  <Button
                    className="mt-6 gap-2"
                    onClick={() => fileInputRef.current?.click()}
                  >
                    <Upload className="w-4 h-4" />
                    Upload Files
                  </Button>
                )}
              </div>
            )}
          </div>
        )}
      </main>
    </div>
  );
}
